

/*HEADER*/

/*carrito de compra cantidad*/

.cart-amount {
  position: absolute;
  color: #f7f4e8;
  font-weight: bold;
  background-color: red;
  top: 25px;
  right: 12px;
  font-size: 16px;
  margin: 0px;
  border-radius: 50%;
  min-width: 22px;
  display: flex;
    justify-content: center;
}

.p-carrito{
display: flex;
color: #fff;
margin: 0;
}


/*estilo carrito*/
.main-carrito{
  min-height: 50vh;
}
.cart {
  position: relative;
  text-align: center;
  margin-top: 2rem;
}

.cart h2 {
  font-size: 24px;
  margin-bottom: 15px;
}

.cart-items {
  list-style: none;
  padding: 0;
  display: grid;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.cart-items li {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin-bottom: 20px;
}

.product-image {
   width: 110px; 
  height: auto;
  border-radius: 8px;
}

.total {
  margin-top: 30px;
}

.clear-cart,
.continue-shopping,
.pay-now {
  background-color: #6bb39b;
  color: #f7f4e8;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-bottom: 10px;
  width: 11rem;
  height: 2.6rem;
}
.continue-shopping{
  width: 15rem;
}

.clear-cart:hover,.continue-shopping:hover{
 background-color: #0b0b0d;
 color: #6bb39b;
}

.payment-methods {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 20px;
}

.payment-methods label {
  font-size: 14px;
}

.total p {
  font-size: 18px;
  margin-bottom: 10px;
  margin-left: 50%;
}
.total {
  margin-bottom: 1rem;
}
